@media(max-width:1000px) {
    .projectsContent {
        /* flex-direction: column; */
        width: 95%;
    }
}

@media(max-width:600px) {
    .projectsContent {
        flex-direction: column;
        width: 95%;
    }

    .projectWindow {
        width: 90%;
        height: 90%;
    }

    .switchImageButton {
        display: none;
    }

    .viewOnGithub {
        padding: 0px;
        padding-inline: 5px;
        font-size: 12px;
        font-weight: 600;
        width: fit-content;
    }

    .endBar {
        display: flex;
        flex-wrap: wrap;
        gap: 5px;
        margin-top: auto;
        margin-bottom: 20px;

        place-content: flex-start;
    }

    .projectBoxInfo>h1 {
        font-size: 14px;
    }

    .projectBoxInfo>text {
        font-size: 12px;
    }

    .buttonSkill {
        font-size: 18px;
    }

    .skillsBar {
        scrollbar-width: none;
    }

    .viewProjectButton {
        font-size: 13px;
        margin-top: 10%;
    }

    .noImageProject>h1 {
        font-size: 16px;
    }

    .asStudentFlag:hover {
        scale: 1;
    }

    .buttonSkill:hover {
        background-color: rgba(0, 0, 0, 0);
        transition-duration: 100ms;
    }
}

@media(max-width:500px) {
    .projects {
        margin-top: 10px;
        margin-bottom: 80px;
    }

}